.btn {
  display: inline-block;
  padding: .375em .75em;
  font-size: 1em;
  font-weight: 400;
  color: #fff;
  line-height: 1.5;
  text-align: center;
  vertical-align: middle;
  background-color: #007bff;
  border: 1px solid #007bff;
  border-radius: .25em;
  user-select: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out; }

.btn:hover {
  text-decoration: none;
  color: #fff;
  background-color: #286090;
  border-color: #204d74; }

.btn.active {
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  color: #fff;
  background-color: #286090;
  border-color: #204d74; }

.btn.disabled,
.btn[disabled] {
  cursor: not-allowed;
  box-shadow: none;
  opacity: .65; }

.text-input {
  margin-bottom: 1em; }

.text-input label {
  display: inline-block;
  margin-bottom: .5em; }

.text-input input {
  display: block;
  width: 100%;
  height: 2.25em;
  padding: .375em .75em;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25em;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out; }

.box {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }

.form {
  width: 300px;
  font-size: 1.2em; }

.form-header {
  width: 100%;
  text-align: center;
  margin-bottom: 1.8em; }

.form-header img {
  width: 80px;
  height: 80px;
  border-radius: 50%; }

.form-message {
  color: #f45454; }

.form-content .text-input {
  padding-left: 0.5em;
  padding-right: 0.5em; }

.form-message,
.form-content .btn {
  width: calc(100% - 1em);
  margin-left: 0.5em;
  margin-left: 0.5em; }
